* {
    margin: 0;
    padding: 0;
}

header {
    height: 60px;
    background: cadetblue;
    width: 100%;
    top: 0;
    margin: auto;
}

main {
    margin-left: 160px;
}

header {
    position: fixed;
}
#sidebar {
    height: 100%;
    width: 160px;
    position: fixed;
    border-right: 1px solid black;
    border-top: 1px solid black;
    text-align: center;
    background-color: azure;
}

li {
    list-style: none;
    border: 1px solid black;
    margin-bottom: 10px;
    padding: 5px;
    cursor: pointer;
    background-color: white;
    color: black;
}

.active > li {
    color: whitesmoke;
    background: black;
}

.list-container {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 60px;
}
.add-note-form {
    margin-top: 90px;
    width: 90%
}
.AddFolder {
    padding-top: 90px;
}
.note-content {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
}
.sidebar-note {
    margin-top: 90px;
    margin-left: 10px;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

